



















































































.order-list-page {
  width: 100vw;
  min-height: calc(100vh - 100px);
  background: #f0f0f0;
  box-sizing: border-box;
  overflow: hidden;
  .order-item {
    background-color: #fff;
    margin-bottom: 20px;
  }
  .order-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 30px 20px 0;
    margin-left: 30px;
    color: #666;
    font-size: 28px;
    border-bottom: 1px solid #f0f0f0;
  }
  .triangle {
    width: 16px;
    height: 16px;
    border-left: 1px solid #999;
    border-top: 1px solid #999;
    margin-left: 5px;
    transform: rotate(135deg) scale(0.8);
  }
  .order-info {
    position: relative;
    display: flex;
    padding: 20px 30px 20px 0;
    margin-left: 30px;
    border-bottom: 1px solid #f0f0f0;
    img {
      width: 114px;
      height: 114px;
    }
  }
  .order-desc {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    justify-content: space-around;
    margin-left: 24px;
    padding: 20px 0;
    font-size: 24px;
    color: #999;
    width: 50vw;
    .delete-box {
      /* 多写一个box的原因是增大点击面积 */
      position: absolute;
      padding: 30px;
      top: 0;
      right: 0;
      .delete {
        font-size: 30px;
        color: #999;
      }
    }
  }
  .showTime {
    font-size: 24px;
  }
  .snack-name {
    font-size: 20px;
    width: 80%;
    color: #333;
  }
  .order-more {
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
    font-size: 28px;
    color: #999;
  }
}
